/** Dispatch event on click outside of nodes */
export function clickOutside(nodes, onClicked){
    onClicked = onClicked||function(){};
    if(Array.isArray(nodes) === false){
      nodes = [nodes];
    }
  
    let destroy = function() {
      document.removeEventListener('click', catchClick, true);
    }
  
    const catchClick = function(event){
      let hasBeenCliked = true;
      let hasBeenClickedOnce = false;
      nodes.map(( node )=>{
        if(typeof node === "string"){
          node = document.querySelector(node);
        }

        if(node === null || typeof node === "undefined"){ return; }
        
        if(node.contains(event.target) === true){
          hasBeenClickedOnce = true;          
        }
        if(node.contains(event.target) === false){
          hasBeenCliked = false;
        }
      }) 
      if(hasBeenClickedOnce === true){
        hasBeenCliked = true;
      }
      if(hasBeenCliked === false){
        setTimeout(() => {
          if(document.contains(event.target)){
            onClicked(destroy);
          }
        }, 25);
      }
    }
    document.addEventListener("click", catchClick, true);
  }