<script>
export let classes = "";
export let classes_content = "";
export let has_circle = false;
export let is_column = false;

</script>
<checkbox-radio role="checkbox" aria-checked="false" class="input-radio">
    <!-- svelte-ignore a11y-label-has-associated-control -->
    <label>
        <slot></slot>
        <checkradio-container class="{classes} {is_column ? "is-flex-direction-column-reverse": ""}">
            {#if has_circle}
                <checkbox-circle class="input-circle flex-center">
                    <div class="inner-circle"></div>
                </checkbox-circle>
            {/if}
            <checkbox-content class="{classes_content}" style="width:100%;">
                <slot name="content"></slot>
            </checkbox-content>
        </checkradio-container>
    </label>
</checkbox-radio>

