<script>
import {HeaderTabsItems, HeaderTabController} from "./HeaderTabController"


/**
 * @type HeaderTabController
 */
export let controller = {};
export let icon_classes = "";
export let title_classes = "";
export let tabs_classes = "";
export let container_classes = "";


function on_change(){
    controller = controller;
}
controller.on_change = on_change;

</script>
<style>
.ul-options{
    justify-content: center !important;
}
.ul-options li{
    width: 33.3333334%;
    max-width: 330px;
    /* border-bottom: 1px solid var(--color-extralightGrey); */
    margin-bottom: -0.5px;
    z-index: 10;
}
.ul-options :global(li:not(.active) h2), .ul-options :global(li:not(.active) i){
    color:  var(--color-mid-grey) !important;
}
.ul-options li a{
    text-decoration: none;
    /* padding-bottom: 20px; */
}
.ul-options :global(.active a){
    color:  var(--color-primary) !important;
}
.ul-options :global(.active){
    border-bottom: 1px solid var(--color-primary) !important;
}
.ul-options i{
    margin-right: 15px;
}
.ul-options li:global( a:hover){
    border-bottom-color: var(--color-extralightGrey) !important;
}

.tabs:global( a){
    border-bottom: none ;
}
li{
    overflow: hidden;
}
@media only screen and (max-width: 1024px){
    .ul-options{
        flex-wrap: wrap;
    }
}
</style>
{#if (controller?.items ?? []).length !== 0}
    <nav class="columns is-multiline is-centered m-0 {container_classes}" style="width:100%;">
        <div class="column is-12 px-0">
            <div class="tabs is-centered" style="width: 100%;">
                <ul class="ul-options">
                    {#each controller?.items ?? [] as item, key}
                        <li data-tab={key+1} class="{item.current ? "active":"has-text-light-grey"} tabs {tabs_classes}" on:click={()=>{ item.set_current(key); }}>
                            <!-- svelte-ignore a11y-missing-attribute -->
                            <a class="button is-ghost">
                                <i class="font-size-32 {item.icon} {icon_classes}"></i>
                                <h2 class="font-size-18 fw500 {title_classes}">{item.title}</h2>
                            </a>
                        </li>
                    {/each}
                </ul>
            </div>
        </div>
    </nav>
{/if}